<!--公用组件：数据表格
 /**
  * @desc EchartsLine
  * @module components/echarts/EchartsLine
  * @desc 一个 echarts 的折线图
  * @author glk
  * @date 2020年02月19日10:00:00
  * @example 调用示例
  *  <EchartsLine></EchartsLine>
  */
-->
<template>
  <div
    class="full-all"
    ref="line"
  ></div>
</template>

<script>
// import
export default {
  name: 'EchartsLine',
  data () {
    return {
      // 定义的每个data 属性必须有注释
      // 注释规则： //
      // echarts 对象
      ec: {}
    }
  },
  props: {
    id: {
      default: '',
      type: String
    }
  },
  components: {},
  // beforeRouteEnter () {
  // },
  // beforeRouteUpdate () {
  // },
  // beforeRouteLeave () {
  // },
  beforeCreate () {
  },
  created () {
  },
  beforeMount () {
  },
  mounted () {
    // 生成 echarts 对象
    this.ec = this.$echarts.init(this.$refs.line)
    // 设置 echarts 对象的配置项，使其生成折线图
    this.ec.setOption({
      series: {
        type: 'pie',
        data: [
          {name: 'A', value: 1212},
          {name: 'B', value: 2323},
          {name: 'C', value: 1919}
        ]
      }
    })
    // 监听 window 的 resize 事件，调整折线图的大小
    this.$common.windowResize(this.resize)
  },
  beforeUpdate () {
  },
  updated () {
  },
  beforeDestroy () {
    // 删除事件监听，减少资源的消耗
    this.$common.removeWindowResize(this.resize)
  },
  destroyed () {
  },
  watch: {},
  computed: {},
  directives: {},
  filter: {},
  methods: {
    // 方法调用不需要注释，方法定义需要注释
    // 方法注释规则 /** */
    /**
     * @desc 使 echarts 折线图根据当前的容器自适应（用于添加到 window 自适应事件中）
    */
    resize () {
      // 如果没有 resize 方法，可能会报错
      if (this.$common.objC(this.ec, 'resize')) {
        this.ec.resize()
      }
    }
  }
}
</script>

<style scoped>

</style>



